import React from 'react';

import { LinkContainer } from "react-router-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css';

import setting from '../Assets/setting.svg';
import notification from '../Assets/notification.svg';
import logout from '../Assets/logout.svg';
import profile from '../Assets/profile.svg';


const Navmenu = () => {
    
        function setnav(){
          const mobilenav = document.querySelector(".mobile-nav");
          const toggle = document.querySelector(".mobile-nav-toggle");
        
          const visibility = mobilenav.getAttribute('data-visible' );
          if(visibility ==="false"){
              mobilenav.setAttribute("data-visible" , true);
              toggle.setAttribute("aria-expanded", true);
              document.querySelector("body").style.overflow='hidden';
          }
          else if(visibility === "true"){
              mobilenav.setAttribute("data-visible" , false);
              toggle.setAttribute("aria-expanded", false);
              document.querySelector("body").style.overflow='auto';
              
          }
        }

  return (
    <nav>
    <LinkContainer to="/"><a className="logo">Film Festival Company</a></LinkContainer>
    <button className="mobile-nav-toggle" aria-controls='selectNav' aria-expanded="false"  onClick={setnav}>
        <span></span>
    </button>
 <div className="d-flex align-items-center">
    <ul className="lg-nav me-4">
    <li><LinkContainer to="/"><a className="active">Festivals</a></LinkContainer></li>
    <li><LinkContainer to="/dashboard"><a>Dashboard</a></LinkContainer></li>
    <li><LinkContainer to="/about"><a>My Projects</a></LinkContainer></li>
    <li><LinkContainer to="/contact"><a>My Submission</a></LinkContainer></li>
    </ul>

        <div className='d-flex icon-nav'>
            <img src={setting} alt="setting" className='me-2 me-md-3'/>
            <img src={notification} alt="setting" className='me-2 me-md-3'/>
            <img src={logout} alt="setting" className='me-2 me-md-3'/>
            <img src={profile} alt="setting" className='me-2 me-md-3'/>

        </div>
    
    <ul id="selectNav" className="mobile-nav" data-visible="false">
    <LinkContainer to="/"><li className="active"><a onClick={setnav}>Festivals</a></li></LinkContainer>
    <LinkContainer to="/dashboard"><li><a onClick={setnav}>Dashboard</a></li></LinkContainer>
    <LinkContainer to="/about"><li><a onClick={setnav}>My Projects</a></li></LinkContainer>
    <LinkContainer to="/contact"><li><a onClick={setnav}>My Submission</a></li></LinkContainer>
    </ul>
    </div>
</nav>
  )
}

export default Navmenu;