import { Container,Row, Col} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import { LinkContainer } from "react-router-bootstrap";
import '../App.css';

const Footer = () => {
  return (
    <footer>
        <Container classNmae="fixed-bottom">
            <Row>
                <Col xs={12} className="d-flex flex-column flex-md-row  justify-content-between text-center py-3">
                <LinkContainer to="/"><a className="logo text-white">Film Festival Company</a></LinkContainer>

                <div className="d-flex flex-column flex-md-row gap-3 gap-md-4 ms-3 me-3 my-5 my-md-0 text-start">
                <LinkContainer to="/"><a className='text-white'>Home</a></LinkContainer>
                <p>How we Work</p>
                <p>View Festivals</p>
                <p>Privacy Policy</p>
                </div>

                <p>All Rights and Terms Reserved</p>
                </Col>

            </Row>

        </Container>

    </footer>
  )
}

export default Footer;